.video {
  margin-top: 125px;

  &__presentation {
    width: 320px;
    height: 427px;
  }

  &__doubt {
    .invitation__slogan();
    text-align: center;
    margin-top: 32px;
  }

  &__clip {
    display: block;
    font-family: "PT Sans Narrow", "Arial", sans-serif;
    font-size: 14px;
    line-height: 24px;
    padding: 0 40px;
  }

  &__video {
    width: 320px;
    height: 201px;
  }

  &__square {
    display: inline-block;
    position: relative;
    width: 280px;
    height: 45px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 51px;
    cursor: none;
  }

  &__square::before {
    display: inline-block;
    position: absolute;
    content: "";
    width: 235px;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.2);
    left: 22px;
    bottom: 20px;
  }


  &__scale {
    display: inline-block;
    position: absolute;
    width: 13px;
    height: 13px;
    background-color: @bg-color;
    border-radius: 50%;
    top: 17px;
    left: 60px;
  }
}

.replay {
  display: inline-block;
  border: none;
  padding: 0;
  margin: 22px 133px 24px 25px;
  cursor: pointer;
  font-size: 0;
  fill: #5c6870;
}

.subtitle {
  display: inline-block;
  border: none;
  padding: 0;
  width: 32px;
  height: 20px;
  margin: 23px 31px 23px 0;
  cursor: pointer;
  font-size: 0;
  fill: #5c6870;
}

.snap {
  display: inline-block;
  border: none;
  padding: 0;
  height: 20px;
  margin: 23px 23px 23px 0;
  cursor: pointer;
  font-size: 0;
  fill: #5c6870;
}

.replay:hover {
  fill: #333333;
}

.subtitle:hover {
  fill: #333333;
}

.snap:hover {
  fill: #333333;
}

.replay:active {
  fill: rgba(51, 51, 51, 0.3);
}

.subtitle:active {
  fill: rgba(51, 51, 51, 0.3);
}

.snap:active {
  fill: rgba(51, 51, 51, 0.3);
}


.toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 280px;
  height: 66px;
  margin: -59px auto;
}


@media (min-width: @tablet-width) {
  .video {
    width: 648px;
    height: 686px;
    margin: 0 auto;

    &__header {
      width: 445px;
      height: 117px;
      margin-right: auto;
      margin-left: auto;
      text-align: center;
      margin-bottom: 67px;
    }

    &__video {
      width: 648px;
      height: 500px;
      margin: 0 auto;
    }

    &__presentation {
      position: relative;
      width: 648px;
      height: 686px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 58px;
    }

    &__film {
      width: 648px;
      height: 500px;
    }

    &__square {
      display: block;
      position: absolute;
      width: 648px;
      height: 93px;
      bottom: 2px;
      margin: 0 auto;
    }

    &__square::before {
      display: inline-block;
      position: absolute;
      content: "";
      width: 389px;
      height: 3px;
      background-color: rgba(255, 255, 255, 0.2);
      left: 93px;
      bottom: 45px;
    }

    &__scale {
      display: inline-block;
      position: absolute;
      width: 13px;
      height: 13px;
      background-color: @bg-color;
      border-radius: 50%;
      top: 40px;
      left: 204px;
    }
  }

  .toggle {
    display: flex;
    justify-content: space-between;
    position: absolute;
    align-items: center;
    width: 648px;
    height: 93px;
    margin: -93px auto;
  }


  .toggle__letgo {
    fill: #ffffff;
    margin: 35px 0 38px 35px;
  }

  .replay {
    background-color: transparent;
    margin: 0;
    fill: #ffffff;
  }

  .toggle__word {
    fill: #ffffff;
    border: none;
  }

  .subtitle {
    margin: 0;
    background-color: transparent;
    margin-left: 470px;
    fill: #ffffff;
  }

  .toggle__rectangle {
    fill: #ffffff;
    margin-left: 31px;
  }

  .snap {
    margin: 0;
    margin-right: 37px;
    background-color: transparent;
    fill: #ffffff;
  }

  .toggle__letgo:hover {
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle__word:hover {
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle__rectangle:hover {
    fill: rgba(255, 255, 255, 0.6);
  }

  .toggle__letgo:active {
    fill: rgba(255, 255, 255, 0.3);
  }

  .toggle__word:active {
    fill: rgba(255, 255, 255, 0.3);
  }

  .toggle__rectangle:active {
    fill: rgba(255, 255, 255, 0.3);
  }
}

