.main-nav {
  position: relative;
  min-width: 320px;
  height: 223px;
}
.main-nav__toggle {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 3;
  display: block;
  width: 70px;
  height: 50px;
  cursor: pointer;
  outline: none;
}
.main-nav--closed .main-nav__toggle {
  top: 225px;
  right: 1px;
  left: auto;
  background-color: #ffffff;
  outline: none;
  border: none;
}
.main-nav--closed .main-nav__toggle:hover,
.main-nav--closed .main-nav__toggle:active {
  background-color: #ffffff;
  outline: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  bottom: 30px;
  left: 30px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 16px 0 0 rgba(129, 179, 210, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
    outline: none;
  }
  .main-nav--opened .main-nav__toggle {
    top: 4px;
    right: 0;
    left: auto;
    background-color: #81b3d2;
    outline: none;
    border: none;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 29px;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
    z-index: 7;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}
